
.search-box {
  position: absolute;
  left: 0;
  right: 0;
  top: 0px;
  bottom: 0;
  background: #fff;
  z-index: 20;
}
.search-box .index_recomme {
  position: relative;
  margin-top: 1.898rem;
  z-index: 0;
  margin-left: 0.533rem;
  margin-right: 0.149rem;
}
.search-box .index_recomme .index_subtitle {
  position: relative;
  padding-top: 0.682rem;
  color: #999;
  line-height: 0.597rem;
  font-size: 0.597rem;
  text-align: left;
}
.search-box .index_recomme .index_items {
  position: relative;
  width: 100%;
  margin-top: 0.17rem;
  margin-bottom: 0.683rem;
  height: 3.925rem;
  overflow: hidden;
}
.search-box .index_recomme .index_items .index_hotword {
  position: relative;
  float: left;
  padding: 0.405rem 0.512rem;
  margin-top: 0.512rem;
  margin-right: 0.363rem;
  border: 0.043rem solid #ccc;
  border-radius: 0.682rem;
}
.search-box .index_recomme .index_items .index_hotword p {
  font-size: 0.555rem;
  line-height: 0.555rem;
  color: #505050;
  text-align: center;
}


