
.showtab {
  position: fixed;
  background-color: #fff;
  top: 1.877rem;
  z-index: 6;
  padding-left: 0.341rem;
  padding-right: 0.298rem;
  overflow: hidden;
}
.showtab .tab-list {
  position: relative;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.showtab .tab-list .a {
  position: relative;
  display: block;
  padding-top: 0.256rem;
  padding-bottom: 0.256rem;
  margin-left: 0.597rem;
  margin-right: 0.597rem;
  background-color: #fff;
}
.showtab .tab-list .a p {
  width: 1.365rem;
  font-size: 0.597rem;
  line-height: 1.28rem;
  text-align: center;
}
.showtab .tab-list .dsj {
  margin: 0 0.298rem;
}
.showtab .tab-list .dsj p {
  width: 1.962rem;
}
.showtab .tab-list .active p {
  color: #fb7299;
  border-bottom: 0.085rem solid #fb7299;
}
.showtab .tab-btn {
  padding: 11px 0;
}
.showtab .tab-btn img {
  display: block;
  margin: 0 auto;
}


