
.navigation {
  top: 3.69733rem;
  position: fixed;
  width: 100%;
  overflow: hidden;
  background: #fff;
  z-index: 4;
}
.navigation .nav-container {
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  padding-left: 0.996rem;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 0.213rem;
  border-bottom: 0.0213rem solid #ccc;
}
.navigation .nav-container .tabg {
  display: inline-block;
  margin-right: 1.024rem;
}
.navigation .nav-container .tabg p {
  font-size: 0.597rem;
  line-height: 1.877rem;
  text-align: center;
  color: #757575;
}
.navigation .nav-container .tabg p.active {
  color: #fb7299;
}


