
.tab {
  position: fixed;
  background-color: #fff;
  width: 100%;
  height: 1.877rem;
  top: 1.856rem;
  z-index: 5;
  overflow: hidden;
}
.tab .tab-left {
  position: relative;
  width: 90%;
  padding-top: 0.256rem;
  overflow: hidden;
}
.tab .tab-left .left-scroll {
  position: relative;
  padding-left: 1.024rem;
  overflow-x: hidden;
  overflow-y: hidden;
  height: 1.877rem;
  white-space: nowrap;
  padding-bottom: 0.213rem;
}
.tab .tab-left .left-scroll .a {
  display: inline-block;
  -webkit-transform: translateZ(0);
  -webkit-perspective: 1000;
  background-color: #fff;
  margin-right: 1.293rem;
}
.tab .tab-left .left-scroll .a p {
  width: 1.365rem;
  font-size: 0.597rem;
  line-height: 1.28rem;
  text-align: center;
}
.tab .tab-left .left-scroll .active p {
  color: #fb7299;
  border-bottom: 0.085rem solid #fb7299;
}
.tab .tab-right {
  position: absolute;
  top: 0;
  width: 1.386rem;
  height: 1.877rem;
  right: 0.256rem;
}
.tab .tab-right img {
  display: block;
  margin: 16px auto;
}


