
.search-b {
  position: fixed;
  width: 93%;
  top: 0;
  left: 0;
  height: 1.8rem;
  padding: 0 3.33%;
  background-color: #fff;
  z-index: 1;
}
.search-b::before {
  content: "";
  display: block;
  height: 0;
  clear: both;
}
.search-b .index_search {
  position: relative;
  width: 88.29%;
  height: 1.28rem;
  margin-top: 0.3rem;
  float: left;
}
.search-b .index_search .index_search_icon {
  position: absolute;
  width: 0.94rem;
  height: 0.94rem;
  top: 0.17rem;
  left: 0.51rem;
  background: url("search-pro.png") no-repeat;
  background-size: 100% 100%;
}
.search-b .index_search .search-input {
  display: block;
  width: 100%;
  height: 100%;
  padding-left: 1.7rem;
  padding-right: 0.4rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 0.55rem;
  border: 0.02133rem solid #e7e7e7;
  border-radius: 0.17rem;
  background-color: #f4f4f4;
  color: #505050;
  cursor: auto;
}
.search-b .index_search .index_close {
  position: absolute;
  width: 1.28rem;
  height: 1.28rem;
  top: 0;
  right: 0;
  background: url("./search-close.png") no-repeat;
  background-size: 0.6rem 0.6rem;
  background-position: 46.7% 46.7%;
  z-index: 10;
}
.search-b .index__cancelBtn {
  position: relative;
  float: right;
  width: 1.28rem;
  margin-top: 0.64rem;
}
.search-b .index__cancelBtn p {
  text-align: right;
  color: #fb7299;
  font-size: 0.6rem;
  line-height: 0.6rem;
}


