
.billbord .hot-box {
  position: relative;
  width: 100%;
  padding-top: 0.08rem;
  height: 15.8rem;
}
.billbord .hot-box .hot-tit {
  position: relative;
  margin-left: 0.5rem;
}
.billbord .hot-box .hot-tit::before {
  content: "";
  display: block;
  height: 0;
  clear: both;
}
.billbord .hot-box .hot-tit .hot-icon {
  position: relative;
  float: left;
  margin-top: 0.6rem;
  width: 1.2rem;
  height: 0.95rem;
  font-size: 27px;
  color: #42c9e8;
}
.billbord .hot-box .hot-tit .hot-recemmend {
  position: relative;
  float: left;
  margin-left: 0.1rem;
  width: 3.4rem;
  font-size: 0.68rem;
  line-height: 2.6rem;
  text-align: left;
  color: #212121;
}
.billbord .hot-box .hot-rank {
  position: absolute;
  right: 0.51rem;
  top: 0.85rem;
  z-index: 5;
}
.billbord .hot-box .hot-rank .paihang {
  position: relative;
  float: left;
  width: 2.4rem;
  margin-top: 0.15rem;
}
.billbord .hot-box .hot-rank .paihang p {
  font-size: 0.6rem;
  color: #ffa726;
  text-align: center;
  line-height: 0.6rem;
}
.billbord .hot-box .hot-rank .more {
  position: relative;
  width: 0.6rem;
  height: 0.6rem;
  margin-top: 0.15rem;
  margin-left: 0.21rem;
  float: left;
}
.billbord .hot-box .hot-rank .more img {
  display: block;
  margin-top: -1px;
}
.billbord .hot-box .hot-content {
  position: relative;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}
.billbord .hot-box .hot-content::before {
  content: "";
  display: block;
  height: 0;
  clear: both;
}
.billbord .hot-box .hot-content .hot-item {
  display: block;
  text-decoration: none;
  position: relative;
  width: 50%;
  float: left;
  margin-bottom: 0.68rem;
}
.billbord .hot-box .hot-content .hot-item .hot-src {
  position: relative;
  width: 7.23rem;
  height: 4.52rem;
  overflow: hidden;
  display: block;
  margin: auto;
  border-radius: 0.26rem;
  -webkit-border-radius: 0.26rem;
  background-color: #e7e7e7;
  z-index: 1;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparent), to(rgba(0,0,0,0.5)));
  background-image: linear-gradient(transparent 50%, rgba(0,0,0,0.5));
}
.billbord .hot-box .hot-content .hot-item .hot-src img {
  position: absolute;
  top: 0;
  z-index: 2;
  width: 100%;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn {
  position: absolute;
  z-index: 3;
  left: 0;
  padding-left: 0.34rem;
  width: 100%;
  bottom: 0;
  height: 0.68rem;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(33,33,33,0)), to(rgba(33,33,33,0.5)));
  background: linear-gradient(180deg, rgba(33,33,33,0), rgba(33,33,33,0.5));
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .play-icon {
  position: relative;
  float: left;
  width: 16px;
  height: 16px;
  top: -8px;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .play-icon .icon {
  width: 16px;
  height: 16px;
  fill: #fff;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .play-sz {
  position: relative;
  float: left;
  margin-left: 0.25rem;
  width: 2rem;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .play-sz p {
  text-align: left;
  font-size: 0.47rem;
  color: #fff;
  line-height: 0.6rem;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .guankan-icon {
  margin-left: 0.61rem;
  float: left;
  width: 18px;
  height: 18px;
  margin-top: -9px;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .guankan-icon .icon {
  width: 18px;
  height: 18px;
  color: #fff;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .guankan-index {
  position: relative;
  float: left;
  margin-left: 0.16rem;
  width: 2rem;
}
.billbord .hot-box .hot-content .hot-item .hot-src .hot-btn .guankan-index p {
  text-align: left;
  font-size: 0.47rem;
  color: #fff;
  line-height: 0.6rem;
}
.billbord .hot-box .hot-content .hot-item .hot-title {
  width: 7.23rem;
  position: relative;
  margin: auto;
  margin-top: 0.21rem;
  height: 1.37rem;
  overflow: hidden;
}
.billbord .hot-box .hot-content .hot-item .hot-title p {
  font-size: 0.56rem;
  color: #212121;
  letter-spacing: 0;
  line-height: 0.69rem;
  text-align: left;
}


