
.videolistbox {
  padding: 1.024rem 0 0;
  background-color: #f3f3f3;
}
.videolistbox h3 {
  padding-left: 0.51rem;
  margin-bottom: 0.43rem;
  font-size: 0.64rem;
  font-weight: 400;
}
.videolistbox .index__loadMore {
  margin-top: 0.51rem;
  width: 100%;
  line-height: 1.73rem;
  font-size: 0.55rem;
  color: #fb7299;
  background-color: #e7e7e7;
  text-align: center;
}
.videolistbox .list-box {
  padding: 0 0.512rem;
  overflow: hidden;
}
.videolistbox .list-box .item {
  display: block;
  position: relative;
  padding: 0.43rem 0 0.43rem 5.5rem;
  height: 3.11rem;
  overflow: hidden;
  border-top: 0.02rem solid #ccc;
}
.videolistbox .list-box .item .img_wrap {
  position: absolute;
  top: 0.43rem;
  left: 0;
  width: 5rem;
  height: 3.11rem;
  border-radius: 0.13rem;
  overflow: hidden;
}
.videolistbox .list-box .item .img_wrap img {
  width: 100%;
  height: 100%;
}
.videolistbox .list-box .item div .index_titlx {
  display: block;
  margin-top: -0.085rem;
  margin-bottom: 0.17rem;
  max-height: 1.54rem;
  line-height: 0.77rem;
  font-size: 0.59rem;
  color: #212121;
  overflow: hidden;
}
.videolistbox .list-box .item div .index_upName {
  display: block;
  margin-bottom: 0.17rem;
  line-height: 0.68rem;
  font-size: 0.51rem;
  color: #757575;
}
.videolistbox .list-box .item .index_infowrap_s {
  line-height: 0.68rem;
  font-size: 0.51rem;
  color: #757575;
}
.videolistbox .list-box .item .index_infowrap_s .index_num {
  float: left;
  width: 50%;
  line-height: 0.68rem;
  font-size: 0.51rem;
  color: #757575;
}
.videolistbox .list-box .item .index_infowrap_s .index_view {
  float: left;
  width: 50%;
  line-height: 0.68rem;
  font-size: 0.512rem;
  color: #757575;
}
.videolistbox .list-box .item:first-child {
  border-top: none;
}


