
.header {
  position: fixed;
  z-index: 20;
  width: 100%;
  height: 1.322rem;
  padding: 0.277rem 0;
  background-color: #fff;
}
.header .logo {
  position: absolute;
  display: block;
  left: 0.778rem;
  top: 50%;
  width: 2.645rem;
  -webkit-transform: translateY(-50%);
}
.header .logo img {
  position: relative;
  display: block;
  width: 100%;
  margin: 0 auto;
}
.header .search {
  position: absolute;
  display: block;
  width: 0.896rem;
  height: 0.896rem;
  right: 42%;
  top: 50%;
  -webkit-transform: translateY(-50%);
}
.header .search .search-icon {
  display: block;
  width: 100%;
  height: 100%;
  fill: #aaa;
}
.header .history {
  position: absolute;
  display: block;
  width: 1.024rem;
  height: 1.024rem;
  right: 30.13%;
  top: 50%;
  border-radius: 50%;
  -webkit-transform: translateY(-50%);
  background: url("./noface.gif") no-repeat;
  background-size: 100% 100%;
  overflow: hidden;
}
.header .donlown {
  position: absolute;
  display: block;
  top: 50%;
  -webkit-transform: translateY(-50%);
  right: 0.427rem;
  width: 3.413rem;
  height: 1.024rem;
  border: 0.021rem solid #fb7299;
  border-radius: 0.17067rem;
  background-color: #fb7299;
}
.header .donlown p {
  font-size: 0.555rem;
  line-height: 1.066rem;
  text-align: center;
  color: #fff;
}


