
.slider {
  min-height: 1px;
}
.slider .slider-group {
  position: relative;
  overflow: hidden;
}
.slider .slider-group .slider-item {
  float: left;
  overflow: hidden;
  white-space: nowrap;
}
.slider .slider-group .slider-item a {
  display: block;
  width: 100%;
}
.slider .slider-group .slider-item img {
  display: block;
  width: 100%;
}
.slider .dots {
  position: absolute;
  bottom: 12px;
  left: 50%;
  margin-left: -50px;
  text-align: center;
  font-size: 0;
  background: rgba(0,0,0,0.5);
  padding: 1px 10px;
  border-radius: 5px;
  z-index: 10;
}
.slider .dots .dot {
  display: inline-block;
  margin: 0 4px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #fff;
}
.slider .dots .dot.active {
  background: #de698c;
}


