
.comment {
  padding-top: 1rem;
  background-color: #f3f3f3;
}
.comment h3 {
  margin-bottom: 0.086rem;
  margin-left: 0.51rem;
  font-size: 0.64rem;
  font-weight: 400;
}
.comment .downLoad {
  display: block;
  margin: 0.13rem 0.51rem 0;
  height: 1.5rem;
  line-height: 1.5rem;
  font-size: 0.56rem;
  color: #fff;
  background-color: #fb7299;
  border-radius: 0.13rem;
  text-align: center;
}
.comment .no_index_commentWrap p {
  text-align: center;
  padding: 1.5rem 0;
  font-size: 0.57rem;
  color: #212121;
}
.comment .index_commentWrap {
  padding: 0 0.51rem;
}
.comment .index_commentWrap .index_commentItem {
  position: relative;
  padding: 0.77rem 0 0.67rem 2.1rem;
  min-height: 1.7rem;
  border-top: 0.02rem solid #ccc;
  overflow: hidden;
}
.comment .index_commentWrap .index_commentItem:first-child {
  border-top: none;
}
.comment .index_commentWrap .index_commentItem:first-child .index_imgWrap {
  position: absolute;
  width: 1.7rem;
  height: 1.7rem;
  top: 0.77rem;
  left: 0;
  border-radius: 50%;
  overflow: hidden;
}
.comment .index_commentWrap .index_commentItem:first-child .index_imgWrap .toux {
  width: 100%;
  height: 100%;
}
.comment .index_commentWrap .index_commentItem:first-child .index_upName {
  display: block;
  max-width: 60%;
  margin-bottom: 0.51rem;
  color: #757575;
  font-size: 0.55rem;
}
.comment .index_commentWrap .index_commentItem:first-child .index_commentTime {
  position: absolute;
  top: 0.81rem;
  right: 0;
  font-size: 0.47rem;
  color: #999;
}
.comment .index_commentWrap .index_commentItem:first-child .index_commentTxt {
  color: #212121;
  line-height: 0.81rem;
  font-size: 0.6rem;
}


