
.recommend {
  position: absolute;
  left: 0;
  top: 3.797rem;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: #f4f4f4;
}
.recommend .recommend-content {
  height: 100%;
  overflow: hidden;
}
.recommend .recommend-content .slider-wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.recommend .recommend-content .slider-wrapper img {
  display: block;
  width: 100%;
}


